
<template>
    <div class="input-group color-picker" ref="colorpicker">
        <input type="text" class="input-vue-color" placeholder="Misol: #000000" v-model="colorValue" @focus="showPicker()" @input="updateFromInput" />
        <span class="input-group-addon color-picker-container">
            <span class="current-color" :style="'background-color: ' + colorValue" @click="togglePicker()"></span>
            <chrome-picker :value="colors" @input="updateFromPicker" v-if="displayPicker" />
        </span>

        <!-- <colorpicker :color="defaultColor" v-model="defaultColor" /> -->
    </div>
</template>

<script>
import { Chrome } from 'vue-color';

export default {
    props: ['color'],
    components: {
		'chrome-picker': Chrome,
	},
	data() {
		return {
			colors: {
				hex: '',
			},
			colorValue: '',
			displayPicker: false,
		}
	},
	mounted() {
		// this.setColor(this.color || '#000000');
	},
	methods: {
		setColor(color) {
			this.updateColors(color);
			this.colorValue = color;
		},
		updateColors(color) {
			if(color.slice(0, 1) == '#') {
				this.colors = {
					hex: color
				};
			}
			else if(color.slice(0, 4) == 'rgba') {
				var rgba = color.replace(/^rgba?\(|\s+|\)$/g,'').split(','),
					hex = '#' + ((1 << 24) + (parseFloat(rgba[0]) << 16) + (parseFloat(rgba[1]) << 8) + parseFloat(rgba[2])).toString(16).slice(1);
				this.colors = {
					hex: hex,
					a: rgba[3],
				}
			}
		},
		showPicker() {
			document.addEventListener('click', this.documentClick);
			this.displayPicker = true;
		},
		hidePicker() {
			document.removeEventListener('click', this.documentClick);
			this.displayPicker = false;
		},
		togglePicker() {
			this.displayPicker ? this.hidePicker() : this.showPicker();
		},
		updateFromInput() {
			this.updateColors(this.colorValue);
		},
		updateFromPicker(color) {
			this.colors = color;
			if(color.rgba.a == 1) {
				this.colorValue = color.hex;
			}
			else {
				this.colorValue = 'rgba(' + color.rgba.r + ', ' + color.rgba.g + ', ' + color.rgba.b + ', ' + color.rgba.a + ')';
			}
		},
		documentClick(e) {
			var el = this.$refs.colorpicker,
				target = e.target;
			if(el !== target && !el.contains(target)) {
				this.hidePicker()
			}
		}
	},
	watch: {
		colorValue(val) {
			if(val) {
				this.updateColors(val);
				this.$emit('input', val);
				//document.body.style.background = val;
			}
		}
	},
}
</script>

<style>
.vc-chrome {
	position: absolute;
	left: 10px;
	z-index: 999;
}
.color-picker{
	position: relative;
	display: flex;
	align-items: center;
}
.color-picker-container{
	padding: 8px;
    width: 15%;
    height: 56px;
    border: 2px solid #e2e1e1;
	border-top-right-radius: 14px;
	border-bottom-right-radius: 14px;
	background: #EEEEEE;
}
.current-color {
	display: inline-block;
	background-color: #000;
	width: 100%;
	height: 100%;
	border-radius: 5px;
	cursor: pointer;
}
.input-vue-color{
	display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    width: 85%;
    height: 56px;
    border: 2px solid #F5F5F5;
	border-right: none;
	border-top-left-radius: 14px;
	border-bottom-left-radius: 14px;
}
.input-vue-color:focus{
	transition: all 0.3s;
	border: 2px solid #3d3dff;
	outline: #3d3dff;
}
.input-vue-color::placeholder{
    color: #c5c4c4;
	font-family: Arial, Helvetica, sans-serif;
}
</style>